.preview {
  width: 25%;
  height: 300px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  margin-top: 25px;
  border: 1px solid #d4d4d4;
  box-shadow: 0px 5px 7px -1px rgba(136, 135, 135, 0.77);
  align-items: center;
}

#tickmarks {
  display: flex;
  justify-content: space-between;
  font-size: 0.6em;
}
